import React, { Component } from 'react';
import { Row, Col, Button } from 'reactstrap';

// import footerBG from 'assets/images/landing/footer-bg.svg';
import fbMessenger from 'assets/images/socialIcons/facebook-messenger.svg';

import styles from './Footer.module.scss';

const socialData = [{
  icon: 'facebook',
  url: 'https://www.facebook.com/infinitefrontierstudio/',
}, {
  icon: 'instagram',
  url: 'https://www.instagram.com/infinitefrontier.sg/',
}]

class Footer extends Component {
  renderEnquire() {
    return (
      <div className={styles.enquireContainer}>
        <div className={styles.enquireInner}>
          <h3>Enquire Now</h3>
          <p>
          That’s all there is to it! We’ll be in touch every step of the way, keeping you in the loop and taking in your feedback without fail. Sound good? Great — get the ball rolling by contacting us today.
          </p>
          <Button
            color="link"
            className={styles.messengerBtn}
            onClick={() => {
              try {
                window.FB.CustomerChat.showDialog();
              } catch(error) {
                //eslint-disable-next-line
              }
            }}
          >
            <img src={fbMessenger} alt="fb-messenger.svg" />
            <p>MESSAGE US!</p>
          </Button>
        </div>
      </div>
    )
  }

  renderFooterInfo() {
    return (
      <div className={styles.footerInfo}>
        <Row className="main-margin w-100 align-items-end mb-4" noGutters>
          <Col xs={7}>
            {/* <div className="d-flex">
              <p>HOME</p>
              <p>ABOUT US</p>
              <p>FAQ</p>
              <p>CHANGE LOG</p>
              <p>CONTACT</p>
            </div> */}
            <div><p>© 2019 INFINITE FRONTIER</p></div>
          </Col>
          <Col xs={5}>
            <div className="d-flex align-items-end justify-content-end">
              {
                socialData.map(social => (
                  <Button
                    className={styles.socialBtn}
                    color="link"
                    onClick={() => { window.open(social.url, '_blank'); }}
                  >
                    <img src={require(`assets/images/socialIcons/${social.icon}.svg`)} alt={social.icon} />
                  </Button>
                ))
              }
            </div>
          </Col>
        </Row>
      </div>
    );
  }

  render() {
    return (
      <div id="enquire" className={styles.footer}>
        {/* <div className={styles.footerBG}>
          <img src={footerBG} alt="footer-bg" />
        </div> */}
        {this.renderEnquire()}
        {this.renderFooterInfo()}
      </div>
    );
  }
}

export default Footer;
