import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';

import PropTypes from 'prop-types';
import SubjectCard from './SubjectCard';
import OtherCard from './OtherCard';

import './NCalculator.scss';

// import Experienced from '../../assets/Experienced.png';

class NCalculator extends Component {
  constructor(props) {
    super(props);

    const { moduleData, setSubjectData } = this.props;
    // console.log(moduleData);
    this.state = {
      isReinitSubjects: moduleData.moduleData.length > 0,
    };

    if (moduleData.moduleData.length === 0) {
      setSubjectData(0, { subjectName: 'English', level: 'N Level', grade: 0 });
      setSubjectData(1, { subjectName: 'Math', level: 'N Level', grade: 0 });
    }

    this.addSubject = this.addSubject.bind(this);
    this.removeSubject = this.removeSubject.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  componentDidUpdate() {
    const { isReinitSubjects } = this.state;
    if (isReinitSubjects) {
      this.setState({
        isReinitSubjects: false,
      });
    }
  }

  addSubject() {
    const { setSubjectData, moduleData } = this.props;
    setSubjectData(moduleData.moduleData.length, { subjectName: '', level: 'N Level', grade: 0 });
  }

  removeSubject(index) {
    const { removeSubjectData } = this.props;
    this.setState({
      isReinitSubjects: true,
    });

    removeSubjectData(index);
  }

  renderSubjects() {
    const { isReinitSubjects } = this.state;
    const { setSubjectData, moduleData } = this.props;
    const result = [];
    for (let i = 0; i < 2; ++i) {
      const updateData = isReinitSubjects ? moduleData.moduleData[i] : null;
      const incomingSubject = i === 0 ? 'English' : 'Math';
      const jsx = (
        <SubjectCard
          key={`card-${i}`}
          incomingSubject={incomingSubject}
          setSubjectData={setSubjectData}
          index={i}
          updateData={updateData}
        />
      );

      result.push(jsx);
    }

    for (let i = 2; i < moduleData.moduleData.length; ++i) {
      const index = i;
      const updateData = isReinitSubjects ? moduleData.moduleData[index] : null;
      const jsx = (
        <SubjectCard
          key={`card-${index}`}
          setSubjectData={setSubjectData}
          removeCallback={this.removeSubject}
          index={i}
          updateData={updateData}
        />
      );

      result.push(jsx);
    }

    return result;
  }

  render() {
    const { setOtherData, moduleData, onSubmit } = this.props;
    return (
      <Row className="subject-container" noGutters>
        <OtherCard
          moduleData={moduleData}
          setOtherData={setOtherData}
        />
        {this.renderSubjects()}
        <Col className="add-button-container" xs={12}>
          <button
            className="add-button"
            onClick={this.addSubject}
          >
            Add
          </button>
        </Col>
        <Col className="submit-button-container" xs={12}>
          <button
            className="submit-button"
            onClick={onSubmit}
          >
            Submit
          </button>
        </Col>
      </Row>
    );
  }
}


NCalculator.defaultProps = {
};

NCalculator.propTypes = {
  setSubjectData: PropTypes.func.isRequired,
  removeSubjectData: PropTypes.func.isRequired,
  setOtherData: PropTypes.func.isRequired,
  moduleData: PropTypes.object.isRequired,
  onSubmit: PropTypes.func.isRequired,
};

export default NCalculator;
