import React, { Component } from 'react';
import { Button } from 'reactstrap';
import PropTypes from 'prop-types';

import Logo from 'assets/images/common/logo.svg';

import styles from './Splash.module.scss';

class Splash extends Component {
  render() {
    const { title } = this.props;

    return (
      <div className={[styles.splashContainer, 'main-margin'].join(' ')}>
        <div className={styles.logoContainer}>
          <img className={styles.mainLogo} src={Logo} alt="INFINTIE FRONTIER" />
          <h1>INFINITE FRONTIER</h1>
        </div>
        <h2>Explainer Videos With Extra Oomph</h2>
        <h6>Build your brand with captivating videos crafted by our in-house heroes</h6>
        <hr />
        <p className={styles.text}>{`Who — We’re visual experts bringing extra oomph to your business with amazing explainer videos.

What — Convert your awesome ideas into great marketing with the help of our diverse team of animators, marketers, and design experts.

Mission — To turn every project we work with into a winner using the power of animated explainer videos. `}</p>
        <div className={styles.buttonContainer}>
          <p>Whoa, sorry to hog the mic! That’s us, but how about telling us a bit about you?</p>
          <Button
            onClick={() => {
              try {
                window.FB.CustomerChat.showDialog();
              } catch(error) {
                //eslint-disable-next-line
                console.log(error);
              }
            }}
          >
            <span>Drop us a line</span>
            <i className="fas fa-arrow-right" />
          </Button>
        </div>
      </div>
    );
  }
}

Splash.defaultProps = {
  title: '',
};

Splash.propTypes = {
  title: PropTypes.string,
};

export default Splash;
