import React, { Component } from 'react';
import { Row, Col } from 'reactstrap';
import PropTypes from 'prop-types';
import MediaQuery from 'react-responsive';

import why1 from 'assets/images/landing/why-1.svg';
import why2 from 'assets/images/landing/why-2.svg';
import why3 from 'assets/images/landing/why-3.svg';

import greenFill from 'assets/images/landing/green-fill.svg';
import redFill from 'assets/images/landing/red-fill.svg';
import twiggy from 'assets/images/landing/twiggy.svg';
import octagon1 from 'assets/images/landing/octagon-1.svg';
import octagon2 from 'assets/images/landing/octagon-2.svg';

import SegmentHeader from '../SegmentHeader'

import styles from './DigitalMarketing.module.scss';

const data = [{
  img: why1,
  title: 'People ❤️ video',
  desc: 'They say a picture is worth a thousand words — but video is worth so much more. Getting your point across quickly is priceless, but explainer videos do just that while driving demand for your brand and turning clicks into sales.',
  fill: greenFill,
}, {
  img: why2,
  title: 'Watching > reading',
  desc: 'Why? Because voice and video have no equal when it comes to capturing attention. Every day, people are becoming less eager to read when they can receive that same information via audio and video. Additionally, the mobility of digital native generations makes them more apt to digest information on the go.',
  fill: redFill,
}, {
  img: why3,
  title: 'Proof in the pudding',
  desc: 'Video makes up over 80% of the internet’s content. People reshare video 10x more than links to text or static images. However, animated explainer videos cost up to 300% less than live action videos, but are just as effective.',
  fill: greenFill,
}]

class DigitalMarketing extends Component {
  renderData(d, index) {
    const imgJSX = (
      <div className={styles.imgContainer}>
        <img src={d.img} alt={d.title} />
      </div>
    )

    const descJSX = (
      <div className={styles.descContainer}>
        <h3>{d.title}</h3>
        <hr />
        <p>{d.desc}</p>
        <div className={styles.wavImg}>
          <img src={index % 2 ? greenFill : redFill} alt="fill-img.png" />
        </div>
      </div>
    )

    return (
      <Col className={styles.data} key={`digital-marketing-${index}`} xs={12}>
        <Row noGutters>
          <Col className="d-flex align-items-center justify-content-center" xs={12} sm={index % 2 ? 6 : 6}>
            <MediaQuery minWidth={576}>
              {index % 2 ? descJSX : imgJSX }
            </MediaQuery>
            <MediaQuery maxWidth={575}>
              {imgJSX}
            </MediaQuery>
          </Col>
          <Col className="d-flex align-items-center justify-content-center" xs={12} sm={index % 2 ? 6 : 6}>
            <MediaQuery minWidth={576}>
              {index % 2 ? imgJSX : descJSX }
            </MediaQuery>
            <MediaQuery maxWidth={575}>
              {descJSX}
            </MediaQuery>
          </Col>
        </Row>
      </Col>
    )
  }

  render() {
    const { title } = this.props;

    return (
      <div id="highlights" className={styles.digitalMarketing}>
        <div className={styles.twiggy}>
          <img src={twiggy} alt="twiggy.svg" />
        </div>
        <div className={styles.octagon1}>
          <img src={octagon1} alt="octagon1.svg" />
        </div>
        <div className={styles.octagon2}>
          <img src={octagon2} alt="octagon2.svg" />
        </div>
        <div className="main-margin">
          <SegmentHeader
            title="Why Explainer Videos"
            subTitle="Explainer Videos"
          />
          <Row noGutters>
            {data.map((d, index) => this.renderData(d, index))}
          </Row>
          <p className={[styles.tagline, 'main-margin'].join(' ')}>Want to get in on the action? We’re ready when you are — contact us.</p>
        </div>
      </div>
    );
  }
}

DigitalMarketing.defaultProps = {
  title: '',
};

DigitalMarketing.propTypes = {
  title: PropTypes.string,
};

export default DigitalMarketing;
