import _ from 'lodash';
import React, { Component } from 'react';
import {
  Row,
  Col,
  Input,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
  UncontrolledDropdown,
} from 'reactstrap';
// import FontAwesome from 'react-fontawesome';
import PropTypes from 'prop-types';

import './NCalculator.scss';

const nGradeArray = [
  '1', '2', '3', '4', '5', 'U', 'Did not attend',
];

const oGradeArray = [
  'A1',
  'A2',
  'B3',
  'B4',
  'C5',
  'C6',
  'D7',
  'E8',
  'F9',
  'U',
  'Did not attend',
];

class SubjectCard extends Component {
  constructor(props) {
    super(props);

    const { incomingSubject } = this.props;

    this.state = {
      subjectName: incomingSubject || '',
      level: 'N Level',
      grade: nGradeArray[0],
      errorString: null,
    };

    this.setData = this.setData.bind(this);
  }

  componentDidMount() {
    const { updateData } = this.props;

    if (!updateData) {
      this.setData();
    }
  }

  componentDidUpdate(prevProps, prevState) {
    const { level, grade } = this.state;
    const { updateData } = this.props;

    if (updateData) {
      this.subjectInput.value = updateData.subjectName;
      // console.log(this.subjectInput);
      this.setState(updateData);
    }

    if (level !== prevState.level) {
      const arrayToUse = level === 'O Level' ? oGradeArray : nGradeArray;
      const gradeToUse = _.findIndex(arrayToUse, item => item === grade) !== -1 ? grade : arrayToUse[0];

      this.setState({
        grade: gradeToUse,
      });
      this.setData(gradeToUse);
    }
  }

  setData(overrideGrade = null) {
    const { subjectName, level, grade } = this.state;
    const { setSubjectData, index } = this.props;

    const gradeToUse = overrideGrade || grade;
    setSubjectData(index, { subjectName, level, grade: gradeToUse });
  }

  render() {
    const {
      level,
      grade,
      subjectName,
      errorString,
    } = this.state;

    const { incomingSubject, removeCallback, index } = this.props;

    const isCore = incomingSubject;
    const subjectHeader = isCore ? 'Core Subject' : 'Subject';
    const coreStyle = isCore ? 'core' : '';
    const nStyle = level === 'N Level' ? 'active' : 'inactive';
    const oStyle = level === 'N Level' ? 'inactive' : 'active';
    const gradeText = grade || 'Grade';
    const arrayToUse = level === 'O Level' ? oGradeArray : nGradeArray;

    return (
      <Col className="module-card" xs={12}>
        <Row className="top" noGutters>
          <p className={`subject-type ${coreStyle}`}>{subjectHeader}</p>
          {
            isCore ?
              null
            :
              <button
                className="delete-card"
                onClick={() => removeCallback(index)}
              >
                <i className="fa fa-times-circle" />
              </button>
          }
        </Row>
        <Row className={`mid ${coreStyle}`} noGutters>
          {
            isCore ?
              <Input
                placeholder="Subject"
                value={incomingSubject}
                disabled
                ref={node => this.subjectInput = node}
              />
            :
              <Input
                ref={node => this.subjectInput = node}
                placeholder="Subject"
                value={subjectName}
                onChange={(e) => {
                  this.setState({ subjectName: e.target.value });
                  setTimeout(this.setData, 10);
                }}
              />
          }
        </Row>
        <Row className="bottom" noGutters>
          <div className="level-container">
            <button
              className={`level-button ${nStyle}`}
              onClick={() => {
                this.setState({ level: 'N Level' });
                setTimeout(this.setData, 10);
              }}
            >
              N Level
            </button>
            <button
              className={`level-button ${oStyle}`}
              onClick={() => {
                this.setState({ level: 'O Level' });
                setTimeout(this.setData, 10);
              }}
            >
              O Level
            </button>
          </div>
          <div className="dropdown-container">
            <p className="grade-text">Grade</p>
            <UncontrolledDropdown>
              <DropdownToggle caret>
                {gradeText}
              </DropdownToggle>
              <DropdownMenu>
                {
                  _.map(arrayToUse, (g, gIndex) => (
                    <DropdownItem
                      key={`grade-${gIndex}`}
                      onClick={() => {
                        this.setState({ grade: g });
                        setTimeout(this.setData, 10);
                      }}
                    >
                      {g}
                    </DropdownItem>
                  ))
                }
              </DropdownMenu>
            </UncontrolledDropdown>
          </div>
        </Row>
        <Row noGutters>
          <p className="error-string">{errorString}</p>
        </Row>
      </Col>
    );
  }
}


SubjectCard.defaultProps = {
  incomingSubject: null,
  removeCallback: () => {},
  updateData: null,
};

SubjectCard.propTypes = {
  incomingSubject: PropTypes.string,
  removeCallback: PropTypes.func,
  setSubjectData: PropTypes.func.isRequired,
  index: PropTypes.number.isRequired,
  updateData: PropTypes.object,
};

export default SubjectCard;
