import React from 'react';
import { Row, Col } from 'reactstrap';

import './Footer.scss';

export default class Footer extends React.Component {
  render() {
    return (
      <Col className="footer-container">
        <Row className="fact-sheet">
          <Col xs={12} sm={4}>
            <p className="title">Grades Legend</p>
            <p>1 - 75% above</p>
            <p>2 - 70% - 74%</p>
            <p>3 - 65% - 69%</p>
            <p>4 - 60% - 64%</p>
            <p>5 - 50% - 59%</p>
            <p>U - Below 50%</p>
          </Col>
          <Col xs={12} sm={8} className="contact">
            <p className="title">We also create animation and motion graphics!</p>
            <p className="title">CONTACT US</p>
            <p>Email - <a href="mailto:support@infinitefrontier.sg"> support@infinitefrontier.sg</a></p>
            <p>© Infinite Frontier Studios</p>
          </Col>
        </Row>
      </Col>
    );
  }
}
