import React from 'react';
import {
  Switch,
  Route,
  withRouter,
} from 'react-router-dom';
import { ROUTES } from 'constants/routes';
import {
  Landing,
  NotFound,
  NCalculator,
} from 'pages';

const Routes = () => (
  <Switch>
    <Route exact path={ROUTES.LANDING.link} component={Landing} />
    <Route exact path={ROUTES.NCALCULATOR.link} component={NCalculator} />
    <Route path="*" component={NotFound} />
  </Switch>
);

export default withRouter(Routes);
