import React, { Component } from 'react';
import PropTypes from 'prop-types';

import purpleWave from 'assets/images/landing/purple-wave.svg';

import SegmentHeader from '../SegmentHeader';

// import whyBG from 'assets/images/landing/why-bg.svg';
import octagon3 from 'assets/images/landing/octagon-3.svg';

import styles from './WhyInfiniteFrontier.module.scss';

const data = [{
  desc: 'At every step of the design process, we seek out your feedback and ensure that your seal of approval marks everything we do.',
}, {
  desc: 'You’re not just a client — but are our partner, too. As partners, we partake in your success by watching your video rack up clicks and conversions.',
}, {
  desc: 'Our videos keep people on your page for longer — up to 85% longer. Ready to roll? Reach out and we’ll be there.',
}];

class WhyInfiniteFrontier extends Component {
  renderData(d, index) {
    return (
      <div
        key={`why-infinite-fontier-${index}`}
        className={styles.dataContainer}
      >
        <div className={styles.dataInner}>
          <h3>{index + 1}</h3>
          <p>{d.desc}</p>
        </div>
      </div>
    )
  }

  render() {
    return (
      <div id="about" className={styles.whyInfiniteFrontier}>
        <SegmentHeader
          title="Why Us"
          subTitle="LASER-FOCUSED ON YOU"
        />
        <hr className="main-margin" />
        {/* <div className={styles.whyImageContainer}>
          <img src={whyBG} alt="why-bg.svg" />
        </div> */}
        <div className={styles.octagon3}>
          <img src={octagon3} alt="octagon3.svg" />
        </div>
        <div className={[styles.dataWrapper, 'main-margin'].join(' ')}>
          {data.map(this.renderData)}
        </div>
      </div>
    );
  }
}

WhyInfiniteFrontier.defaultProps = {
};

WhyInfiniteFrontier.propTypes = {
};

export default WhyInfiniteFrontier;
