import {
  SET_OTHER_DATA,
  SET_SUBJECT_DATA,
  REMOVE_SUBJECT_DATA,
  SET_STATE,
} from '../actions/nCalculatorActions';

const initialState = {
  firstTime: null,
  cca: null,
  age: null,
  moduleData: [],
  isCalculator: true,
};

const nCalculator = (state = initialState, action) => {
  switch (action.type) {
    case SET_OTHER_DATA: {
      return {
        ...state,
        firstTime: action.data.firstTime,
        cca: action.data.cca,
        age: action.data.age,
      };
    }
    case SET_SUBJECT_DATA: {
      const newModuleData = [...state.moduleData];
      newModuleData[action.data.index] = action.data.moduleData;
      return {
        ...state,
        moduleData: newModuleData,
      };
    }
    case REMOVE_SUBJECT_DATA: {
      const newModuleData = [...state.moduleData];
      newModuleData.splice(action.data, 1);
      return {
        ...state,
        moduleData: newModuleData,
      };
    }
    case SET_STATE: {
      return {
        ...state,
        isCalculator: action.data,
      };
    }
    default:
      return state;
  }
};

export default nCalculator;
