import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { Nav, NavItem, NavLink, NavbarBrand, Collapse, Button } from 'reactstrap';

import Logo from 'assets/images/common/logo.svg';

import styles from './Navbar.module.scss';

class Navbar extends Component {
  state={
    isFull: false,
    isLogo: false,
    collapsed: false,
  };

  componentDidMount() {
    window.addEventListener('scroll', this.onMovement);
    this.onMovement();
  }

  componentWillUnmount() {
    window.removeEventListener('scroll', this.onMovement);
  }

  toggleNavbar = () => {
    const { collapsed } = this.state;

    this.setState({ collapsed: !collapsed });
  }

  onMovement = (e) => {
    const { isFull, isLogo } = this.state;

    if (!isFull && window.scrollY > 0) {
      this.setState({
        isFull: true,
      });
    }

    if (isFull && window.scrollY <= 0) {
      this.setState({
        isFull: false,
      });
    }

    if (!isLogo && window.scrollY > 200) {
      this.setState({
        isLogo: true,
      });
    }

    if (isLogo && window.scrollY <= 200) {
      this.setState({
        isLogo: false,
      });
    }
  }

  render() {
    const { isFull, isLogo, collapsed } = this.state;

    return (
      <div className={[styles.nav, isFull ? styles.full : ''].join(' ')}>
        <Nav
          className={[styles.navInner, "main-margin d-none d-lg-flex", isLogo ? styles.innerShift : ''].join(' ')}
        >
          <NavbarBrand
            className={[styles.logo, isLogo ? styles.logoVisible : ''].join(' ')}
            href="/"
          >
            <img src={Logo} alt="logo-nav" />
          </NavbarBrand>
          <NavItem>
            <NavLink href="#home">Home</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="#portfolio">Portfolio</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="#highlights">Highlights</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="#process">Process</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="#about">About</NavLink>
          </NavItem>
          <NavItem>
            <NavLink href="#enquire">Enquire</NavLink>
          </NavItem>
        </Nav>
        <div
          className={["main-margin d-block d-lg-none", styles.navResp, isFull ? styles.full : ''].join(' ')}
        >
          <div className="d-flex align-items-center">
            <NavbarBrand
              className={[styles.logo, styles.logoVisible].join(' ')}
              href="/"
            >
              <img src={Logo} alt="logo-nav" />
            </NavbarBrand>
            <div className="flex-1" />
            <Button
              color="link"
              onClick={this.toggleNavbar}
              className={styles.bars}
            >
              <i className="fas fa-bars" />
            </Button>
          </div>
          <Collapse isOpen={collapsed} navbar>
            <Nav
              className={styles.navInnerResp}
            >
              <NavItem>
                <NavLink href="#home">Home</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="#portfolio">Portfolio</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="#highlights">Highlights</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="#process">Process</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="#about">About</NavLink>
              </NavItem>
              <NavItem>
                <NavLink href="#enquire">Enquire</NavLink>
              </NavItem>
            </Nav>
          </Collapse>
        </div>
      </div>
    );
  }
}

Navbar.defaultProps = {
};

Navbar.propTypes = {
};

export default Navbar;
