import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Container } from 'reactstrap';

import PropTypes from 'prop-types';
import NCalculator from './components/NCalculator';
import Result from './components/Result';
import Footer from './components/Footer/Footer';
import { setOtherData, setSubjectData, removeSubjectData, setState } from 'actions/nCalculatorActions';

class NCalculatorContainer extends Component {
  constructor(props) {
    super(props);
    this.onSubmit = this.onSubmit.bind(this);
  }

  onSubmit() {
    this.props.setState(false);
  }

  render() {
    const { moduleData } = this.props;

    if (moduleData.isCalculator) {
      return (
        <div className="main">
          <Container>
            <NCalculator
              setSubjectData={this.props.setSubjectData}
              removeSubjectData={this.props.removeSubjectData}
              setOtherData={this.props.setOtherData}
              moduleData={moduleData}
              onSubmit={this.onSubmit}
              setState={this.props.setState}
            />
          </Container>
          <Footer />
        </div>
      );
    }

    return (
      <div className="main">
        <Container>
          <Result
            moduleData={moduleData}
            setState={this.props.setState}
            onBack={() => this.props.setState(true)}
          />
        </Container>
        <Footer />
      </div>
    );
  }
}


NCalculatorContainer.defaultProps = {
};

NCalculatorContainer.propTypes = {
  moduleData: PropTypes.object.isRequired,
  setSubjectData: PropTypes.func.isRequired,
  removeSubjectData: PropTypes.func.isRequired,
  setOtherData: PropTypes.func.isRequired,
  setState: PropTypes.func.isRequired,
};

const mapStateToProps = state => ({
  moduleData: state.nCalculator,
});

const mapDispatchToProps = dispatch => ({
  setSubjectData: (index, moduleData) => {
    dispatch(setSubjectData(index, moduleData));
  },
  removeSubjectData: (index) => {
    dispatch(removeSubjectData(index));
  },
  setOtherData: (firstTime, cca, age) => {
    dispatch(setOtherData(firstTime, cca, age));
  },
  setState: (isCalculator) => {
    dispatch(setState(isCalculator));
  },
});

export default connect(mapStateToProps, mapDispatchToProps)(NCalculatorContainer);
