import _ from 'lodash';
import React, { Component } from 'react';
import { Row, Col, Table } from 'reactstrap';

import PropTypes from 'prop-types';

import './Result.scss';

const nToIteMapping = {
  1: 1,
  2: 1,
  3: 2,
  4: 3,
  5: 4,
  U: 5,
  'Did not attend': null,
};

const oToNMapping = {
  A1: 1,
  A2: 1,
  B3: 1,
  B4: 2,
  C5: 2,
  C6: 2,
  D7: 3,
  E8: null,
  F9: null,
  U: null,
  'Did not attend': null,
};

class Result extends Component {
  constructor(props) {
    super(props);

    const { moduleData } = this.props;

    const mappedData = [];
    let nLevelTotal = 0;
    let iteTotal = 0;
    let b3 = 0;
    let el = 0;
    let math = 0;
    let best3Count = 0;
    let isB3LessEqual5 = true;

    const elMa = [moduleData.moduleData[0], moduleData.moduleData[1]];
    let b3Data = moduleData.moduleData.slice(2);
    b3Data = _.sortBy(b3Data, (o) => {
      if (o.level === 'N Level') {
        const nlevel = parseInt(o.grade, 10);
        return _.isNumber(nlevel) && !_.isNaN(nlevel) ? nlevel : 9999;
      }

      const nOlevel = oToNMapping[o.grade];
      return _.isNumber(nOlevel) && !_.isNaN(nOlevel) ? nOlevel : 9999;
    });
    const sortedData = [...elMa, ...b3Data];

    for (let i = 0; i < sortedData.length; ++i) {
      const data = sortedData[i];
      const { level, grade } = data;
      const isNlevel = level === 'N Level';

      let nLevel = null;
      let oLevel = null;
      let ite = null;

      if (isNlevel) {
        nLevel = grade === 'Did not attend' ? null : grade;
        ite = nToIteMapping[grade];
      } else {
        oLevel = grade === 'Did not attend' ? null : grade;
        nLevel = oToNMapping[grade];
        if (nLevel) {
          ite = nToIteMapping[nLevel];
        }
      }

      const nlevelScore = parseInt(nLevel, 10);
      const iteScore = parseInt(ite, 10);

      if (_.isNumber(nlevelScore) && !_.isNaN(nlevelScore)) {
        if (i === 0) {
          el = nlevelScore;
        } else if (i === 1) {
          math = nlevelScore;
        } else if (best3Count < 3) {
          b3 += nlevelScore;
          best3Count += 1;
          if (nlevelScore > 5) {
            isB3LessEqual5 = false;
          }
        }
      }

      if (ite && i < 5 && _.isNumber(iteScore) && !_.isNaN(iteScore)) {
        iteTotal += iteScore;
      }

      mappedData.push({
        subject: data.subjectName,
        nLevel,
        oLevel,
        ite,
      });
    }

    nLevelTotal = b3 + el + math - moduleData.cca;
    iteTotal -= moduleData.cca;
    // console.log(best3Count, b3, el, math);

    const is5Subjects = el !== 0 && math !== 0 && best3Count >= 3;

    const eligibilityList = [
      {
        isDisplay: is5Subjects && best3Count >= 3 && nLevelTotal <= 11 && el <= 3,
        desc: 'PFP (Poly Foundation Program)',
      },
      {
        isDisplay: is5Subjects && best3Count >= 3 && el <= 3 && math <= 4 && isB3LessEqual5 && nLevelTotal <= 19,
        desc: 'DPP (Direct Poly Program) Group 1',
      },
      {
        isDisplay: is5Subjects && best3Count >= 3 && el <= 4 && math <= 4 && isB3LessEqual5 && nLevelTotal <= 19,
        desc: 'DPP (Direct Poly Program) Group 2',
      },
      {
        isDisplay: is5Subjects && best3Count >= 3 && el <= 5 && math <= 5 && isB3LessEqual5 && nLevelTotal <= 19,
        desc: 'Secondary 5',
      },
      {
        isDisplay: is5Subjects && best3Count >= 3 && (nLevelTotal <= 21 || (el + b3) <= 14 || (math + b3)) && moduleData.firstTime === 'Yes' && moduleData.age < 17,
        desc: 'Repeat 4 N(A)',
      },
      {
        isDisplay: is5Subjects && best3Count >= 3 && nLevelTotal <= 19,
        desc: 'Higher Nitec',
      },
      {
        isDisplay: true,
        desc: 'Nitec',
      },
    ];

    this.state = {
      mappedData,
      nLevelTotal,
      iteTotal,
      eligibilityList,
      is5Subjects,
    };
    // console.log(moduleData, mappedData, nLevelTotal, iteTotal);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  renderResults() {
    const {
      mappedData,
      nLevelTotal,
      iteTotal,
    } = this.state;

    const { moduleData } = this.props;

    const table = _.map(mappedData, (data, index) => {
      const style = index < 5 ? 'best-3' : '';
      return (
        <tr key={`score-${index}`}>
          <td>{data.subject}</td>
          <td>{data.oLevel || '-'}</td>
          <td><div className={style}><p>{data.nLevel || '-'}</p></div></td>
          <td>{data.ite || '-'}</td>
        </tr>
      );
    });

    const ccaPoints = moduleData.cca > 0 ? `-${moduleData.cca.toString()}` : 0;
    table.push((
      <tr className="cca" key="cca-total">
        <td>CCA</td>
        <td />
        <td>{ccaPoints}</td>
        <td>{ccaPoints}</td>
      </tr>
    ));

    table.push((
      <tr className="score-total" key="score-total">
        <td>Total</td>
        <td />
        <td><div className="nlevel-total"><p>{nLevelTotal}</p></div></td>
        <td>{iteTotal}</td>
      </tr>
    ));

    return table;
  }

  renderEligibilityList() {
    const { eligibilityList } = this.state;
    return _.reduce(eligibilityList, (result, item) => {
      const newResult = [...result];
      if (item.isDisplay) {
        const jsx = (
          <Col className="eligibility" xs={12} key={item.desc}>
            {item.desc}
          </Col>
        );

        newResult.push(jsx);
      }

      return newResult;
    }, []);
  }

  render() {
    const { is5Subjects } = this.state;
    const { moduleData, onBack } = this.props;
    const firstTime = moduleData.firstTime === 'Yes' ? 'first time taking N-Level.' : 'and took N-Level before.';
    const passed5SubjectsText = is5Subjects ? '' : 'I did not pass 5 or more subjects.';
    return (
      <Row className="subject-container" noGutters>
        <Col className="module-card" xs={12}>
          <Row className="top" noGutters>
            <Col className="subject-type core" xs={12}>My Result</Col>
            <Col className="info core" xs={12}>
              <span>{'I\'m a '}</span>
              {moduleData.age}
              <span> years old </span>
              {firstTime}
            </Col>
          </Row>
          <Row className="mid table" noGutters>
            <Table borderless>
              <thead>
                <tr>
                  <th>Subjects</th>
                  <th>O-Level</th>
                  <th>Higher Nitec</th>
                  <th>Nitec</th>
                </tr>
              </thead>
              <tbody>
                {this.renderResults()}
              </tbody>
            </Table>
          </Row>
        </Col>
        <Col className="module-card" xs={12}>
          <Row className="top" noGutters>
            <p className="subject-type core">Eligibility</p>
          </Row>
          <Row className="mid" noGutters>
            <p className="info core"><u>{passed5SubjectsText}</u></p>
            {this.renderEligibilityList()}
          </Row>
        </Col>
        <Col className="submit-button-container back" xs={12}>
          <button
            className="submit-button"
            onClick={onBack}
          >
            Back
          </button>
        </Col>
      </Row>
    );
  }
}


Result.defaultProps = {
};

Result.propTypes = {
  moduleData: PropTypes.object.isRequired,
  onBack: PropTypes.func.isRequired,
};

export default Result;
