import React, { Component } from 'react';
import PropTypes from 'prop-types';

import { } from 'reactstrap';
import ReactSlickCarousel from 'components/ReactSlickCarousel/ReactSlickCarousel';

import SegmentHeader from '../SegmentHeader';

import styles from './Portfolio.module.scss';

const data = [
  'https://www.youtube.com/embed/71U66I9OKP4',
  'https://www.youtube.com/embed/igoJiLsKNOY',
  // 'https://www.youtube.com/embed/wRJ07fthwyg',
  // 'https://www.youtube.com/embed/oKkexeqhip4',
  // 'https://www.youtube.com/embed/hUJ_3vZhZK0',
]

class Portfolio extends Component {
  render() {
    return (
      <div id="portfolio" className={styles.productionProcess}>
        <SegmentHeader
          title="WE DO"
          subTitle="PORTFOLIO"
        />
        <ReactSlickCarousel
          lazyLoad
          // autoplay
          dots
          slidesToShow={2}
          className="white-sliders-padded"
          responsive={[{
            breakpoint: 992,
            settings: {
              slidesToShow: 1,
            },
          }, {
            breakpoint: 1200,
            settings: {
              slidesToShow: 2,
            },
          }]}
        >
          {
            data.map((d, index) => (
              <div className={styles.videoContainer} key={`portfolio-video-${index}`}>
                <div className={styles.videoInner}>
                  <iframe height="315" width="450" src={d} />
                </div>
              </div>
            ))
          }
        </ReactSlickCarousel>
      </div>
    );
  }
}

Portfolio.defaultProps = {
};

Portfolio.propTypes = {
};

export default Portfolio;
