export const SET_OTHER_DATA = 'SET_OTHER_DATA';
export const SET_SUBJECT_DATA = 'SET_SUBJECT_DATA';
export const REMOVE_SUBJECT_DATA = 'REMOVE_SUBJECT_DATA';
export const SET_STATE = 'SET_STATE';

export const setOtherData = (firstTime, cca, age) => ({
  type: SET_OTHER_DATA,
  data: {
    firstTime,
    cca,
    age,
  },
});

export const setSubjectData = (index, moduleData) => ({
  type: SET_SUBJECT_DATA,
  data: {
    index,
    moduleData,
  },
});

export const removeSubjectData = index => ({
  type: REMOVE_SUBJECT_DATA,
  data: index,
});

export const setState = isCalculator => ({
  type: SET_STATE,
  data: isCalculator,
});
